<template>
<v-card :hover='$vuetify.breakpoint.smAndUp' class='cursor pa-1'>
  <v-card-title primary-title class='pa-2 ma-1'>
    <div>
      <p class='mt-0 mb-1 grey--text text--darken-1 subheading'>{{ info.author }}</p>
      <p class='b600 mb-2 primary--text' style='font-size: 18px'>
        {{ info.title }}
      </p>
      <p class='my-1 grey--text text--darken-2 b600' style='font-size: 15px'>{{ info.where }}</p>
      <p class='my-1 grey--text text--darken-2'>Vol. {{ info.vol }}, No. {{ info.no }}, pp. {{ info.pp }}, {{ info.year }}. <span v-if='info.cited' class='mt-2 mb-0 ml-2 caption'>Cited by {{ info.cited }}</span></p>
      
      <v-divider class='my-3' />
      <p class='mt-0 mb-2 grey--text text--darken-1 subheading'>
        Key statements 
        <v-btn flat small color='primary' class='body-1 my-0 b600' @click='onLang'>
          <v-icon small class='mr-2'>fa-language</v-icon>
          {{ isKR ? '영어' : '한글' }}로 보기
        </v-btn>
      </p>
      
      <v-slide-y-reverse-transition>
        <div v-show='show'>
          <p v-for='(x, i) in key' :key='i' class='my-1 px-1 grey--text text--darken-3' style='font-size: 15px; text-align: justify; line-height: 1.3; font-family: "Roboto"'>
            <v-icon color='primary' class='caption mr-2' style='vertical-align: middle;'>fa-check</v-icon>{{ x }}
          </p>
        </div>
      </v-slide-y-reverse-transition>
    </div>
  </v-card-title>
  
  <v-card-actions class='mt-0 pt-0'>
    <v-btn flat color='primary'>
      <v-icon class='body-1 mr-2'>fa-binoculars</v-icon>
      정보
    </v-btn>
    <v-btn flat color='primary'>
      <v-icon class='body-1 mr-2'>fa-paper-plane</v-icon>
      DOI
    </v-btn>
  </v-card-actions>
</v-card>
</template>


<script>
export default {
  props: ['ix', 'info'],
  data: () => ({
    show: true,
    key : null,
    isKR: false,
  }),
  
  methods: {
    onLang() {
      this.show = false
      _.delay(() => {
        this.isKR = !this.isKR
        this.key  = this.isKR ? this.info.keyKR : this.info.keyEN
        this.show = true
      }, 300)
    }
  },
  mounted() {
    this.key = this.info.keyEN
  }
}
</script>
